.mobileNav{
    position:fixed;
    height:100vh;
    width:100%;
    top:0;
    left:0;

    background-color: rgba(255, 255, 255, 0.808);
    
    display:flex;
    justify-content: center;
    align-items: center;
}

.mobileNavUL{
    display:flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height:100%;
    width:100%;
    li{
        padding:5%;

    }
}